"use client";

import { useState } from "react";
import { generateCoverLetter } from "@/actions/coverletter";
import { Button } from "@/components/ui/button";

export default function AICoverLetterPage() {
  const [formData, setFormData] = useState({
    jobTitle: "",
    companyName: "",
    jobDescription: "",
    industry: "",
    experience: "",
    skills: "",
    bio: "",
  });

  const [loading, setLoading] = useState(false);
  const [coverLetter, setCoverLetter] = useState("");

  async function handleGenerate() {
    try {
      setLoading(true);
      const skillsArray = formData.skills.split(",").map((s) => s.trim());
      const result = await generateCoverLetter({
        ...formData,
        skills: skillsArray,
      });
      setCoverLetter(result); // âœ… display it directly here
    } catch (error) {
      console.error("Error:", error);
      setCoverLetter("Failed to generate cover letter.");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="container mx-auto py-8 space-y-6">
      <h1 className="text-2xl font-bold">AI Cover Letter Generator</h1>

      <div className="grid gap-4 max-w-xl">
        {Object.keys(formData).map((key) => (
          <input
            key={key}
            type="text"
            placeholder={key}
            value={formData[key]}
            onChange={(e) =>
              setFormData({ ...formData, [key]: e.target.value })
            }
            className="border rounded-md p-2"
          />
        ))}
      </div>

      <Button onClick={handleGenerate} disabled={loading}>
        {loading ? "Generating..." : "Generate Cover Letter"}
      </Button>

      {coverLetter && (
        <div className="mt-6 border rounded-lg p-4 bg-muted/50">
          <h2 className="text-lg font-semibold mb-2">ðŸ“„ Generated Cover Letter</h2>
          <div
            className="prose"
            dangerouslySetInnerHTML={{ __html: coverLetter.replace(/\n/g, "<br>") }}
          />
        </div>
      )}
    </div>
  );
}
